@import url(http://fonts.cdnfonts.com/css/seravek-medium);
button{background:transparent;border:2px solid #FFF;border-radius:8px;box-sizing:border-box;color:#FFF;cursor:pointer;font-weight:700;height:4.5vh;min-height:42px;min-width:84px;width:6vw}

.search-wrapper{display:flex;height:30%;justify-content:center;max-width:80vw;min-width:224px}.search-wrapper .search-input{background:transparent;border:2px solid #FFF;border-radius:8px;box-sizing:border-box;color:#FFFFFF;font-weight:bold;height:4.5vh;margin-right:.5vw;min-height:42px;min-width:110px;padding:10px 14px;width:15vw}.search-wrapper .search-input::-webkit-input-placeholder{color:#9F9F9F}.search-wrapper .search-input:-ms-input-placeholder{color:#9F9F9F}.search-wrapper .search-input::placeholder{color:#9F9F9F}.search-wrapper .search-input::-webkit-search-cancel-button{-webkit-appearance:none}.search-wrapper .search-button{margin-left:.5vw}

.notification-container{animation:toast-in-right .7s;box-sizing:border-box;font-size:16px;position:fixed;right:12px;top:12px;transition:transform .6s ease-in-out;z-index:999999}.notification{align-items:flex-start;animation:toast-in-right .7s;-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);background:rgba(0,0,0,0.8);border:2px solid #606060;border-radius:8px;box-shadow:0 0 10px #040404;color:#FFFFFF;display:flex;flex-direction:column;justify-content:center;max-width:70vw;opacity:.9;overflow:hidden;padding:20px 15px 10px 10px;transition:transform .6s ease-in-out;width:365px}.notification:hover{box-shadow:0 0 5px #fff;cursor:pointer;opacity:1}.notification-title{font-size:18px;font-weight:700;margin-bottom:6px;margin-top:0}.notification-message{margin:0;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}@keyframes toast-in-right{from{transform:translateX(100%)}to{transform:translateX(0)}}

.loading-wrapper{align-items:center;background:rgba(0,0,0,0.5);display:flex;flex-direction:column;height:100vh;justify-content:center;left:0;position:absolute;top:0;width:100vw}.loading-wrapper .loading-image{animation:rotate 1s linear infinite;background:linear-gradient(rgba(20,255,233,0.239216), rgba(255,235,59,0.25098), rgba(255,0,224,0.121569), rgba(83,212,21,0), rgba(48,19,202,0.14902), rgba(224,23,23,0.231373));border:2px solid #606060;border-radius:8px;box-shadow:0 0 50px #ff00e08a;box-sizing:border-box;max-width:80%}.loading-wrapper .loading-text{color:#FFF;font-size:24px;line-height:33px;margin-top:4vh}@keyframes rotate{0%{-webkit-filter:hue-rotate(0deg);filter:hue-rotate(0deg)}100%{-webkit-filter:hue-rotate(360deg);filter:hue-rotate(360deg)}}

.location-info{display:flex;flex-direction:column;margin-bottom:6vh}.location-info .planet{color:#8C8C8C;font-size:14px;line-height:19px}.location-info .planet-dimension{color:#FFFFFF;font-size:20px;line-height:25px}.location-info .dimension{color:#D3D3D3;font-size:16px;line-height:24px}.location-info .residents{color:#8C8C8C;font-size:14px;line-height:19px;margin-top:1.5vh}.location-info .residents .icon{margin-right:13px}@media (min-width: 600px){.location-info .planet-dimension{font-size:28px;line-height:38px}.location-info .dimension{font-size:18px;line-height:25px}}

.character-card{background:#1B1B1B;border:2px solid #606060;border-radius:8px;box-shadow:0px 8px 5px 0px #000;box-sizing:border-box;height:224px;overflow:hidden;position:relative;width:224px;cursor:pointer}.character-card:hover{border:2px solid #CBD736;box-shadow:0px 0px 0px 3px #C9D636,0px 0px 5px 4px #C9D636,inset 0px 0px 0px 1px rgba(0,0,0,0.6);box-sizing:border-box}.character-card.expanded{height:173px;width:178px}.character-card.expanded:hover{border:none;box-shadow:none}@media (min-width: 600px){.character-card.expanded{height:72.5vh;width:28vw}}.character-card .character-image-wrapper{height:100%;width:100%}.character-card .character-image-wrapper img{height:100%;width:100%}.character-card .character-image-wrapper img.grayscale{-webkit-filter:grayscale(100%);filter:grayscale(100%)}.character-card .character-info{-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);background:rgba(0,0,0,0.8);bottom:0;box-sizing:border-box;color:#FFF;display:flex;flex-direction:column;font-family:'Seravek', sans-serif;height:55px;justify-content:center;padding:7px 13px;position:absolute;width:100%}.character-card .character-info .name{font-size:20px;font-weight:700;line-height:25px;text-overflow:ellipsis;overflow:hidden;white-space:nowrap;cursor:pointer}.character-card .character-info .species{font-size:12px;font-weight:400;line-height:15px}

.floating-card{display:flex;justify-content:center;position:absolute;width:100%;z-index:10}.modal-wrapper{background:#000;border-radius:16px;display:flex;flex-direction:column;justify-content:flex-end;margin-top:88px;overflow:hidden;width:100%}.modal-wrapper .modal-close-button{border:none;font-size:26px;left:0;position:absolute;top:4px;z-index:10;display:flex}.modal-wrapper .modal-close-button:before{content:'X'}.modal-wrapper .character-image-blurred{border:1px solid #161616;height:13%;opacity:0.4;overflow:hidden;width:100%}.modal-wrapper .character-image-blurred img{-webkit-filter:blur(30px);filter:blur(30px);height:100%;width:100%}.modal-wrapper .details-wrapper{border:1px solid #161616;box-sizing:border-box;height:87%;overflow-y:auto;padding:8vh 5.2vw 8vh 4.9vw;width:100%}.modal-wrapper .about-wrapper{margin-bottom:7vh}.modal-wrapper .about-wrapper .personal-info{color:#D3D3D3;font-size:16px;line-height:22px}@media (min-width: 600px){.floating-card{align-items:center;height:100%;left:0;width:32vw}.modal-wrapper{flex-direction:row;height:100%;margin-top:0;width:93%}.modal-wrapper .modal-close-button{left:calc(7% + 20px);top:20px;border:2px solid #FFF;font-size:1rem;justify-content:center;align-items:center}.modal-wrapper .modal-close-button:before{content:'Close'}.modal-wrapper .character-image-blurred{height:100%;width:37%}.modal-wrapper .details-wrapper{height:100%;width:63%}}

.characters-list{display:grid;grid-gap:24px;grid-template-rows:repeat(6, -webkit-min-content);grid-template-rows:repeat(6, min-content);height:63vh;justify-content:center;overflow-x:hidden;overflow-y:auto;width:100vw}@media (min-width: 600px){.characters-list{grid-template-columns:repeat(2, -webkit-min-content);grid-template-columns:repeat(2, min-content)}}@media (min-width: 900px){.characters-list{grid-template-columns:repeat(3, -webkit-min-content);grid-template-columns:repeat(3, min-content)}}@media (min-width: 1200px){.characters-list{grid-template-columns:repeat(4, -webkit-min-content);grid-template-columns:repeat(4, min-content)}}

.pages{align-items:center;bottom:0;box-sizing:border-box;display:flex;font-family:'Seravek', sans-serif;height:7vh;justify-content:center;position:fixed;text-align:center;width:100%}.pages .page{border:none;color:#909090;cursor:pointer;font-size:20px;font-weight:500;line-height:27px;max-width:12vw;width:65px}.pages .page:hover{color:#FFF}.pages .current{color:#CBD736}.pages .arrows{align-items:center;border:none;color:#3E3E3E;cursor:pointer;display:flex;font-size:35px;min-width:auto;padding:0;width:auto}.pages .arrows:hover{color:#FFF}

.empty-state{align-items:center;box-sizing:border-box;display:flex;flex-direction:column;justify-content:center;padding:0 25%;text-align:center;width:100%}.empty-state .empty-image{width:32vmin}.empty-state .empty-image img{width:inherit}.empty-state .empty-title{color:#FFFFFF;font-family:'Seravek', sans-serif;font-size:3vmax;font-weight:bold;line-height:3vmax;margin-bottom:3vh;margin-top:5vh}.empty-state .empty-subtitle{color:#FFFFFF;font-family:'Seravek', sans-serif;font-size:2vmax;line-height:3vmax}@media (min-width: 600px){.empty-state .empty-title{font-size:2vmax;line-height:2vmax}.empty-state .empty-subtitle{font-size:1.5vmax;line-height:2vmax}.empty-state .empty-image{width:25vmin}}

.home{animation:blinker-home 0.5s linear;background:url(/search-ricky-and-morty/static/media/background.4c7a7001.svg) no-repeat center;background-size:cover;font-size:16px;font-style:normal;height:100vh;line-height:22px;width:100vw}@keyframes blinker-home{0%{opacity:0.3}30%{opacity:0.5}30%{opacity:0.3}40%{opacity:0.2}50%{opacity:1}60%{opacity:0.6}70%{opacity:0.3}80%{opacity:1}90%{opacity:0.2}100%{opacity:1}}.home.loading{-webkit-filter:blur(15px);filter:blur(15px)}.header{align-items:center;animation:blinker-header 0.5s linear;display:flex;flex-direction:column;height:30vh}@keyframes blinker-header{0%{opacity:0}30%{opacity:0.5}30%{opacity:0.7}30%{opacity:0.3}40%{opacity:0}50%{opacity:0.4}60%{opacity:0}70%{opacity:0.3}80%{opacity:1}90%{opacity:0}100%{opacity:1}}.logo{height:70%;max-width:80vw}*{font-family:'Nunito', sans-serif}

html,body{margin:0;padding:0}body{background:#1E1E1E;overflow:hidden}*:focus{outline:0 !important}h1{color:#CBD736;font-size:13px;font-weight:400;letter-spacing:0.65em;line-height:18px;margin-bottom:1.8vh;text-transform:uppercase}::-webkit-scrollbar{width:7px}::-webkit-scrollbar-track{background:transparent}::-webkit-scrollbar-thumb{background:#3E3E3E;border:1px solid #aaa7a7;border-radius:5px}::-webkit-scrollbar-thumb:hover{background:#555}

